import * as React from 'react';
import { motion, useTransform } from 'framer-motion';
import { useScrollPosition } from '@n8tb1t/use-scroll-position';

import { Transform, backgroundPos } from './logos-scroll';
import useScroll from '../../lib/use-scroll';
import Scene from './scene';
import Content from './content';
import Conventions from './conventions';
import Tagline from './tagline';

// associated logo images import
import stateEmblem from "./../../../images/state-emblem-pk.svg";
import undpLogo from "../../../images/undp-logo-30.svg";
import gefLogo from "../../../images/gef.png";


export default () => {

	const [y, setY] = React.useState<Transform>(
		backgroundPos(0)
	);

	useScroll((pos) => {
		if (pos.y < 500) {
			setY(backgroundPos(
				pos.y
			));
		}
	});

	return (
		<div
			id="hero_homepage"
			className="hero hero-homepage is-primary"
		>
			<Scene />
			{/* <Content /> */}
			{/* <Conventions /> */}
			{/* <Tagline /> */}
		</div>
	);
}


const AssociatedLogos = ({ top }: { top: string }) =>
	<motion.div
		className="associated-logos"
		style={{ top }}>

		<figure className="figure">
			<img src={stateEmblem} />
		</figure>

		<figure className="figure">
			<img src={undpLogo} />
		</figure>

		<figure className="figure">
			<img src={gefLogo} />
		</figure>
	</motion.div>
