// initial y-positions(in percentage) for logo backgrounds
const LogosPos = 50;

// pixels for each percentage point
const PixelPerPercentage = 50;

// size in percentage (guess)
const LogoSize = 12;

// breakpoint for percentage based logos
const PercentageBreakpoint = 100 - LogoSize;


export type Transform = {
	cover: number	// in pixels

	// rest in pecentage
	associatedLogos: number
}

export function backgroundPos(y: number): Transform {
	const nLogosPos = (y / PixelPerPercentage) + LogosPos;

	// if any of them reaches 100 - Logo size (at the bottom)
	// terminate the animation
	if(nLogosPos > PercentageBreakpoint) {
		return {
			cover: y/2,
			associatedLogos: PercentageBreakpoint,
		}
	}

	return {
		cover: y/2,
		associatedLogos: nLogosPos,
	}
}