import * as React from 'react';

const dateOptions = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };


export type NewsItem = {
	title: string,
	date: Date,
	content: string,
	category: string,
	image: string,
	location: string,
}

export const old = ({ item }: { item: NewsItem }) => (
	<div className='columns news-outline'>
		<div
			className='column news-outline-image'
			style={{
				backgroundImage: `url(${item.image})`
			}}>
			<span className="tag is-link">{item.category}</span>
			<br />
			<span className="tag is-dark is-size-7 news-date">{item.date.toLocaleDateString("en-US", dateOptions)}</span>
		</div>
		<div className='column news-outline-description'>
			<h3 className="title is-4">
				<a href="#">
					{item.title}
				</a>
			</h3>
			<p className='content'>{item.content}</p>
		</div>
	</div>
);

export default ({ items }: { items: [NewsItem, NewsItem, NewsItem] }) => (
	<div>
		<div className="container">
			<div className="columns news-outline-flexbox">
				{items.map((item, key) =>
					<div className="column is-4" key={key}>
						<a className="box news-outline-item is-clearfix" href="#" onClick={(e) => e.preventDefault()}>
							<figure className="figure"><img src={item.image} alt={items.title} /></figure>
							<div className="padded">
								<div className="level">
									<div className="level-left">
										<div className="level-item">
											<span className="news-outline-tag">{item.category.toUpperCase()}</span>
										</div>
									</div>
									<div>
										<div className="level-right">
											<div className="level-item">
												<div className="news-outline-location">
													<span className="icon">
														<i className="fa fa-map-marker"></i>
													</span>
													<small>
														{item.location}
													</small>
												</div>
											</div>
										</div>
									</div>
								</div>
								<small className="news-outline-date">{item.date.toLocaleDateString("en-US", dateOptions)}</small>

								{/* content */}
								<h2 className="sub-heading news-outline-heading">
									{item.title}
								</h2>
								{/* <a href="#" className="button is-success">
									<span className="icon is-small">
										<i className="fa fa-angle-right"></i>
									</span>
									<span>Read More</span>
								</a> */}

							</div>
						</a>
					</div>
				)}
			</div>
		</div>
	</div>
);
