import React, { useEffect, useState } from 'react';


import barley from "../../../images/slmp-undp-flickr.jpg";
// import forest from '../../../images/foggy-forest.jpg';
// import leafs from '../../../images/leafs.jpg';
// import hunza from '../../../images/hunza-valley-2.png';

/*
sub scenes
	
	Background image slider	
		background-layers contains image background
		that change their position 0% - 100% 
			-> animation duration 1-2 secs
			-> interval 3-5 secs
*/


const AnimationInterval = 90000; // miliseconds
const CloudsIdxs: string[] = [];

const Backgrounds = [
	// forest,
	// leafs,
	barley
];

export default () => {

	const [active, setActive] = useState(0);

	// making hero transparent
	useEffect(() => {
		const container = document.getElementById("hero_homepage");
		const styles = window.getComputedStyle(
			container,
		);

		const backgroundImage = styles.backgroundImage;
		const backgroundColor = styles.backgroundColor;

		container.style.backgroundColor = 'transparent';
		container.style.backgroundImage = 'none';

		function cleanUp() {
			if (container) {
				container.style.backgroundImage = backgroundImage;
				container.style.backgroundColor = backgroundColor;
			}
		}

		return cleanUp;

	}, []);

	// image change timer
	useEffect(() => {
		const interval =
			window.setInterval(() => {

				setActive(
					(active + 1) % Backgrounds.length
				);

			}, AnimationInterval);

		return () => {
			window.clearInterval(interval)
			console.log('removing hero image changer interval');
		}
	});

	return (
		<div className='hero-scene'>
			<div className='background-layers'>
				{Backgrounds.map((v, idx) => (
					<div
						key={idx}
						className={`layer ${active == idx ? 'active' : ''}`}
						style={{
							backgroundImage: `url(${v})`,
						}}>
					</div>
				))}
				{/* <div className={`layer clouds ${CloudsIdxs.indexOf(active) >= 0 ? 'active' : ''}`}></div> */}
				{/* <div className="layer brightner active"></div> */}
				{/*<div className="hero-black-backdrop"></div>*/}
			</div>
		</div>
	);
}
