import { useRef, useEffect } from 'react';
import hasPassiveEventSupport from '../feature-detect/passive-event-listeners';

type Handler = (pos: {x: number, y:number}) => void;

export default function useScroll(handler: Handler) {
	useEffect(() => {

		let latestKnownScrollPos = {
			x: 0,
			y: 0,
		};

		let ticking = false;

		function onScroll() {
			latestKnownScrollPos = {
				x: window.scrollX,
				y: window.scrollY
			};
			requestTick();
		}

		function requestTick() {
			if (!ticking) {
				window.requestAnimationFrame(update);
			}
			ticking = true;
		}

		function update() {
			// resetting the tick so the next 
			// scroll event can be delegated 
			// to this (update) function from
			// requestTick
			ticking = false;
			handler(latestKnownScrollPos);
		}

		window.addEventListener(
			'scroll', 
			onScroll,
			hasPassiveEventSupport ? {passive:true} : false 
		);

		// clean up
		return () => {
			window.removeEventListener(
				'scroll', 
				onScroll,
				false,
			);
		}
	}, []);
}